.login{
    position: relative;
    height: 100vh;
   background:url(https://static.standard.co.uk/2022/11/16/10/netflix-s.jpg?width=1200&auto=webp&quality=75);
   background-size: cover;
}
.login__logo{
    width: 100px;
    position: fixed;
    top:0;
    left: 10px;
}
.login__button{
    background-color: #fc0a0a;
    padding: 10px 20px;
    color:#fff;
    border: 0;
    text-transform: uppercase;
    border-radius: 5px;
    position: fixed;
    top: 10px;
    right: 10px;
    cursor: pointer;
}
.login__body{
    color: #fff;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    text-align: center;
    left: 0;
    right: 0;
}
.login__body h1{
    font-size: 30px;
    margin-bottom:20px;
}
.login__body h2{
    font-size: 22px;
    margin-bottom: 10px;
    font-size: 400;
}
.login__body h3{
    font-size: 16px;
    margin-bottom: 30px;
    font-size: 400;
}
.login__body .login__form{
    display: flex;
    align-items: center;
    justify-content: center;
}
.login__body .login__form input{
    width: 300px;
    height: 40px;
    padding: 0px 15px;
}
.login__body .login__form button{
    height: 45px;
    border: 0;
    padding: 0px 10px;
    background-color: #fc0a0a;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 1px;
    cursor: pointer;
    color: #fff;
}
.login__gradient{
    width: 100%;
    z-index: 1;
    height: 100vh;
    background: rgba(0,0,0,0.4);
    background-image: linear-gradient(to top,
     rgba(0,0,0,0.8),
     rgba(0,0,0,0) 60%,
     rgba(0,0,0,0)100%
     );
}
.signUpScreen{
    max-width: 300px;
    padding: 70px;
    background-color: rgba(0,0,0,0.85);
    margin-left: auto;
    margin-right: auto;
}
.signUpScreen form{
    display: grid;
    flex-direction: column;
}
.signUpScreen form input{
    outline: 0;
    height: 30px;
    border:0;
    margin-bottom: 15px;
    border-radius: 5px;
    padding: 5px 15px;
}
.signUpScreen form button{
 padding: 16px 20px;
font-size: 16px;
 color: #fff;
 border: 0;
 border-radius: 5px;
 background-color: #e50914;
 cursor: pointer;
 margin-bottom: 20px;
}
.signUpScreen form h4 {
    color: gray;
}
.signUpScreen form h4 span{
    color:#fff;
}
.signUpScreen form h4:hover span{
    text-decoration: underline;
    cursor: pointer;
}

.nav{
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 50px;
    z-index: 1;
    box-sizing: border-box;
    transition: all 0.5s ease-in 0s;
}
.nav__content{
    display: flex;
    justify-content: space-between;
    
}
.nav__content .logo{
width: 80px;
position:fixed;
cursor: pointer;
top: 10px;
left: 20px;

}
.nav__avatar{
    width: 30px;
    position:fixed;
    cursor: pointer;
    top: 10px;
    right: 20px;
    
}
.nav__black{
    background-color: #000;
}
.banner{
    height: 400px;
    position: relative;
    object-fit: contain;
    padding-top: 20px;
   color:#fff;
}
.banner__content{
    margin-left: 30px;
   padding-top: 140px;
    height:200px;
}
.banner__description{
    width: 500px;
    line-height: 1.3;
    padding-top: 20px;
    font-size: 16px;
}
.banner__buttons button{
    cursor: pointer;
    color:#fff;
    background-color: red;
    outline: 0;
    border: 0;
    font-weight: 700;
    border-radius: 5px;
    padding: 10px 30px;
    margin-right: 10px;
    margin-top: 10px;

}
.banner__buttons button:hover{
    background-color: #fff;
    color:#000;
    
}
.banner__fade{
    height: 150px;
    background-image: linear-gradient(180deg,transparent, rgba(27,27,27,0.61), #111);
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
}
.row{
    color: #fff;
    margin-left: 20px;
}
.row__posters{
    display:flex;
    overflow: hidden;
    overflow-x: scroll;
    padding: 20px;
    
}
.row__posters::-webkit-scrollbar{
 display: none;
}
.rowposter{
    max-height: 100px;
    object-fit: contain;
    margin-left: 10px;
    width:100%;
    cursor: pointer;
    transition:-webkit-transform 450ms;
    transition:transform 450ms;
    transition:transform 450ms, -webkit-transform 450ms;
  }
  .rowposter:hover{
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
  }
  .largeimage{
    max-height: 250px;
  }
*{
  margin:0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

